@keyframes anim-rotate {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }

.spinner {
  display: inline-block;
  font-size: 4em;
  height: 1em;
  line-height: 1;
  margin: .5em;
  animation: anim-rotate 2s infinite linear;
  color: #333;
  text-shadow: 0 0 0.25em rgba(255, 255, 255, 0.3); }

.spinner--steps {
  animation: anim-rotate 1s infinite steps(8); }

.spinner--steps2 {
  animation: anim-rotate 1s infinite steps(12); }

#easel-file-picker .easel-file-browser {
  height: 100%;
  margin-bottom: 0;
  margin-top: 0;
  min-height: 200px;
  padding: 1rem;
  width: auto; }
  #easel-file-picker .easel-file-browser.modal-dialog {
    margin-top: 2rem; }
  #easel-file-picker .easel-file-browser .modal-header {
    border-bottom: 1px solid #f0f0f0; }
  #easel-file-picker .easel-file-browser .modal-footer {
    border-top: 1px solid #f0f0f0; }
    #easel-file-picker .easel-file-browser .modal-footer.vertical-center .item-count {
      flex: 1 0 0;
      text-align: left; }
    #easel-file-picker .easel-file-browser .modal-footer.vertical-center .buttons {
      flex: 4 0 0; }
  #easel-file-picker .easel-file-browser .word-wrappable {
    word-wrap: break-word; }
  #easel-file-picker .easel-file-browser .easel-file-picker-list {
    height: 60vh;
    overflow-y: scroll; }
    #easel-file-picker .easel-file-browser .easel-file-picker-list .table tbody tr:last-child td {
      padding-bottom: 7px; }
    #easel-file-picker .easel-file-browser .easel-file-picker-list .table tbody tr td {
      transition: background-color 0.15s ease-in-out 0.05s;
      padding-left: 7px; }
      #easel-file-picker .easel-file-browser .easel-file-picker-list .table tbody tr td a {
        margin-left: 0.5em; }
    #easel-file-picker .easel-file-browser .easel-file-picker-list .table tbody tr th:first-child {
      padding-left: 7px; }
  #easel-file-picker .easel-file-browser .easel-file-picker-sidebar {
    height: 60vh;
    overflow-y: auto;
    border-left: 1px solid #f0f0f0; }
  #easel-file-picker .easel-file-browser .easel-file-picker-preview-table {
    margin-top: 1em; }
    #easel-file-picker .easel-file-browser .easel-file-picker-preview-table td {
      border-width: 0; }
      #easel-file-picker .easel-file-browser .easel-file-picker-preview-table td.description {
        text-transform: uppercase;
        font-weight: 500; }
      #easel-file-picker .easel-file-browser .easel-file-picker-preview-table td.file-value {
        word-break: break-all; }

.alert {
  padding-left: 30px;
  font-size: 13px; }

.alert span {
  cursor: pointer; }

.alert:not(.alert-dismissible) {
  padding-right: 30px; }

.alert.alert-dismissable {
  padding-right: 44px; }

.alert-inverse {
  background-color: #333333;
  border-color: transparent;
  color: #ffffff; }

.alert-inverse hr {
  border-top-color: transparent; }

.alert-inverse .alert-link {
  color: #e6e6e6; }

.growl-animated.alert-inverse {
  box-shadow: 0 0 5px rgba(51, 51, 51, 0.5); }

.growl-animated.alert-info {
  box-shadow: 0 0 5px rgba(33, 150, 243, 0.5); }

.growl-animated.alert-success {
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.5); }

.growl-animated.alert-warning {
  box-shadow: 0 0 5px rgba(255, 193, 7, 0.5); }

.growl-animated.alert-danger {
  box-shadow: 0 0 5px rgba(244, 67, 54, 0.5); }
